a.gflag {
    vertical-align: middle;
    font-size: 32 px;
    padding: 1 px 0;
    background-repeat: no - repeat;
    background-image: url(//gtranslate.net/flags/32.png);
}

a.gflag img {
    border: 0;
}

a.gflag:hover {
    background-image: url(//gtranslate.net/flags/32a.png);
}